body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #ccc;
}

p {
  margin-bottom: 0;
  font-weight: normal;
}

::selection {
  background: #111;
}

.ant-collapse-borderless {
  background: #fff;
}

:root {
  --green-color: #4caf50;
  --pink-color: #e91e63;
  --purple-color: #673ab7;
  --blue-color: #2196f3;
}

.trigger {
  font-size: 24px;
  line-height: 64px;
  cursor: pointer;
  transition: color .3s;
  color: white;
}

.trigger:hover {
  color: var(--green-color);
}

.ant-card {
  box-shadow: 2px 4px 12px 2px rgba(21, 21, 21, 0.15);
}

.recent-publications {
  box-shadow: none;
}

.recent-publications .action {
  text-align: right;
  margin: 0;
}

.ant-card-meta-title {
  overflow: auto;
  white-space: normal;
}

.black-card>.ant-card-body * {
  color: white;
}

.black-card>.ant-card-body>p {
  margin: 0;
}

.ant-divider-inner-text {
  color: inherit !important;
}

.ant-tabs-nav .ant-tabs-tab:hover {
  color: var(--green-color);
}

.ant-tabs-nav .ant-tabs-tab-active {
  color: var(--green-color);
}

.ant-tabs-ink-bar {
  background-color: var(--green-color);
}

.ant-tabs {
  color: black;
}

.ant-tabs-bar {
  border-bottom: 1px dashed black;
}

.ant-tabs-tab {
  text-transform: uppercase;
}

.anticon {
  color: inherit !important;
}

a {
  color: inherit !important;
}

a:hover {
  color: var(--green-color) !important;
}

@media only screen and (max-width: 768px) {
  h2 {
    font-size: 14px;
  }
  h3 {
    font-size: 12px;
  }
}

.ant-notification-notice-icon-info, .ant-message-info .anticon, .ant-message-loading .anticon {
  color: var(--blue-color) !important;
}

.twoline-progess .ant-progress-text {
  top: 46% !important;
}

.ant-collapse>.ant-collapse-item>.ant-collapse-header {
  font-size: 16px;
  font-weight: bolder;
}